<template>
    <div class="row mhs-wrap">
       <swiper :options="swiperOption" ref="mySwiper">
            <swiper-slide v-for="(v,i) of showImages" :key="i">
                <img :src="v.image_800">
            </swiper-slide>
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>

export default {
    data() {
        return {
            swiperOption: {
                autoplay: 2000,
                loop: true,
                pagination: ".swiper-pagination",
                autoplayDisableOnInteraction: false
            }
        };
    },
    computed:{
        showImages(){
            //?vuex?????
            return this.$store.getters.swiperImages;
        }
    },
    mounted() {
        this.$store.dispatch("loadSwiperData")
    }
};
</script>

<style lang="less">
.mhs-wrap {
  img {
    width: 100%;
  }
}
</style>


