<template>
<div class="container gdb-wrap">
    <ul class="row clear">
        <li class="clear">
            <div>
                <i class="iconfont">&#xe668;</i>
                <span>收藏</span>
            </div>
            <div>
                <i class="iconfont">&#xe64f;</i>
                <span>小店</span>
            </div>
        </li>
        <li class="gdb-addCart" @click="addToCart">
            <span>加入购物车</span>
        </li>
        <li class="gdb-doBuy">
            <span>立即购买</span>
        </li>
    </ul>
</div>
</template>

<script>
export default {
  props:{
      addCartGoods:null
  },
  methods:{
      addToCart(){
          this.$store.commit("setShoppingCart",{query:"add",datas:this.addCartGoods});
        //   this.$router.push({path:"/simple/ShoppingCart"});
        this.$router.go(-1);
      }
  }

}
</script>

<style lang="less">
.gdb-wrap{
    position: fixed;
    bottom:0;
    left:0;
    height:0.8rem;
    >ul{
        @length:100%;
        height:@length;
        >li{
            width:@length/3;
            height:@length;
            float:left;
            >div{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }
        }
        li:first-child{
            >div{
                width:@length/2;
                height:@length;
                float:left;
                color:rgb(94, 94, 94);

            }
        }
        .gdb-addCart,.gdb-doBuy{
            line-height: 0.8rem;
            text-align: center;
            font-size: 0.25rem;
        }
        .gdb-addCart{
            background-color:rgb(255, 230, 232);
            color:rgb(255, 87, 119);
        }
        .gdb-doBuy{
            background-color:rgb(255, 87, 119);
            color:#fff;;
        }

    }
}

</style>



