<template>
    <div class="container">
        <TopControl></TopControl>
        <GoodsShowTable :searchBy="searchBy"></GoodsShowTable>

    </div>
</template>

<script>
import TopControl from "../globalComponents/TopControl"
import GoodsShowTable from "./GoodsShowTable"

export default {
    components:{
        TopControl,
        GoodsShowTable
    },
    computed:{
        searchBy(){
            return {
                name:'loadTypeShowData',
                contents:{
                    fcid:this.$route.params.fcid,
                    title:this.$route.params.title
                }
            }
        }
    },
    mounted(){
        document.title=this.$route.params.title;
    },
    beforeRouteLeave (to, from, next) {
        //清零搜索信息
        this.$store.commit("setGoodsTypes",[]);
        this.$store.commit("setGoodsList",[]);
        next();
    }
  
}
</script>

<style lang="less">

</style>


